.products {
  width: 100%;
}

.products-table {
  margin:50px 0;
}


.accordion {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
  display: flex;
}

.title {
  display: flex;
  flex:10 0 0;
  font-weight: bold;
}

.active, .accordion:hover {
  background-color: #ccc;
}

.accordion:after {
  content: '\002B';
  color: #777;
  font-weight: bold;
  float: right;
  margin-left: 5px;
}

.active:after {
  content: "\2212";
}

.panel {
  width: 100%;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  &:last-child {
    border: none;
  }
  div {
    padding: 0 18px;
    border-bottom: 1px solid #c7c7c7;
  }
}

